import React, { useEffect, useState } from 'react'

const Table = () => {

    const [data, setDtate] = useState([])

    const getCovidData = async () => {
        const res = await fetch('https://api.covid19india.org/data.json')
        const actualData = await res.json()
        console.log(actualData.statewise)
        setDtate(actualData.statewise)
    }

    useEffect(() => {
        getCovidData()
    }, [])

    return (
        <div className="table-responsive">
            <table className="table">
                <thead className="table-dark">
                    <tr>
                        <th scope="col">State</th>
                        <th scope="col">Confirmed</th>
                        <th scope="col">Recovered</th>
                        <th scope="col">Deaths</th>
                        <th scope="col">Active</th>
                        <th scope="col">Updated</th>
                    </tr>
                </thead>
                <tbody>

                    {
                        data.map((currelem, ind) => {
                            return (
                                <tr key={ind}>
                                    <th className='text-start'>{currelem.state}</th>
                                    <td>{currelem.confirmed}</td>
                                    <td>{currelem.recovered}</td>
                                    <td>{currelem.deaths}</td>
                                    <td>{currelem.active}</td>
                                    <td>{currelem.lastupdatedtime}</td>
                                </tr>
                            )
                        })
                    }



                </tbody>
            </table>
        </div>
    )
}

export default Table
